:root {
  background: black;
}

.head {
    position:relative;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: black;
    font-size: 2rem;
    font-family: 'Archivo', sans-serif;
    color: hsl(32, 98%, 64%); 
}

.body {
    position:relative;
    width: 100vw;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: black;
    font-size: 2rem;
    font-family: 'Archivo', sans-serif;
    color: hsl(32, 98%, 64%);
   }
   
   .bodybody {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: black;
    font-size: 1rem;
    font-family: 'Archivo', sans-serif;
    color: white;
   }

   .navbar {
    width: 100vw;
    background: black;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    /* text-align: center; */
    font-size: 1.5rem;
    font-family: 'Archivo', sans-serif;
    color: white;
   }

   .divbar {
    width: 80vw;
    display: flex;
    flex-direction: row;
    align-self: center;
    align-items: center;
    justify-content: space-evenly;
    align-items: stretch;

   }

   .footer {
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-self: flex-end;
    text-align: center;
    background: black;
    font-size: 1rem;
    font-family: 'Archivo', sans-serif;
    color: hsl(32, 98%, 64%); 
   }